@font-face {
  font-family: "Computer Modern";
  src: url("./fonts/ComputerModern/woff/cmunrm-webfont.woff2") format("woff2"),
    url("./fonts/ComputerModern/woff/cmunrm-webfont.woff") format("woff"),
    url("./fonts/ComputerModern/otf/cmunrm.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Computer Modern";
  src: url("./fonts/ComputerModern/woff/cmunbx-webfont.woff2") format("woff2"),
    url("./fonts/ComputerModern/woff/cmunbx-webfont.woff") format("woff"),
    url("./fonts/ComputerModern/otf/cmunbx.otf") format("opentype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Computer Modern";
  src: url("./fonts/ComputerModern/woff/cmunti-webfont.woff2") format("woff2"),
    url("./fonts/ComputerModern/woff/cmunti-webfont.woff") format("woff"),
    url("./fonts/ComputerModern/otf/cmunti.otf") format("opentype");
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: "Computer Modern";
  src: url("./fonts/ComputerModern/woff/cmunbi-webfont.woff2") format("woff2"),
    url("./fonts/ComputerModern/woff/cmunbi-webfont.woff") format("woff"),
    url("./fonts/ComputerModern/otf/cmunbi.otf") format("opentype");
  font-weight: bold;
  font-style: italic;
}

html {
  scroll-behavior: smooth;
}

#root {
  position: relative;
  min-height: 100vh;
}

#content {
  padding: 2vh 0 calc(2vh + 30px);
}
